<template>
  <section>
    <presentable-steps :active="0"></presentable-steps>
    <el-alert
      title="该资源已创建过presentable"
      type="success"
      v-show="done"
      show-icon>
    </el-alert>
    <el-tabs v-model="activeName" type="border-card">
      <el-tab-pane v-for="(tabData, indexOuter) in tabList"
                   :key="indexOuter"
                   :name="tabData.resourceId">
        <span slot="label">
          {{tabData.resourceName}}

          <el-tooltip class="item" effect="dark" content="已选中合同" placement="top" v-show="tabData.checked">
            <i class="el-icon-circle-check-outline icon-tip"></i>
          </el-tooltip>

          <el-tooltip class="item" effect="dark" content="已创建过合同" placement="top" v-show="tabData.created">
            <i class="el-icon-circle-check icon-tip"></i>
          </el-tooltip>

        </span>
        <div class="p-name">
          <label>presentable name:</label>
          <el-input v-model="tabData.presentableName"
                    style="width: 400px"
                    placeholder="请输入presentable名"></el-input>
        </div>

        <div class="step-1 op-step">
          <div class="step-row">
            <div class="step-circle">1</div>
            <div class="step-title">选择合同segment</div>
          </div>
          <el-table
            :data="tabData.data.policy">
            <el-table-column type="expand">
              <template slot-scope="scope">
                <pre>{{scope.row._formatSegmentText}}</pre>
              </template>
            </el-table-column>
            <el-table-column
              label="segmentId"
              prop="segmentId">
            </el-table-column>
            <el-table-column
              label="">
              <template slot-scope="props">
                <el-radio v-model="tabData.selected"
                          :label="props.$index"
                          @change="segmentChangeHandler(tabData)">
                  <span title="placeholder"></span>
                </el-radio>
                <el-tooltip class="item" effect="dark" content="该策略已经创建过合同" placement="top"
                            v-show="props.row.created">
                  <i class="el-icon-info"></i>
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="取消选择" placement="top"
                            v-show="tabData.selected===props.$index">
                  <i class="el-icon-circle-close-outline"
                     @click="segmentChangeHandler(tabData, false)"></i>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </div>

        <div class="step-2 op-step">
          <div class="step-row">
            <div class="step-circle">2</div>
            <div class="step-title">创建user policy</div>
          </div>
          <presentable-policy v-model="tabData.userPolicy"></presentable-policy>
        </div>
      </el-tab-pane>
      <div class="form-ft">
        <el-button @click="submit" :disabled="!selected">提交</el-button>
      </div>
    </el-tabs>
  </section>
</template>

<script>
  import NodePolicySignment from './index'

  export default NodePolicySignment
</script>

<style lang="less" scoped>
  @import "index.less";
</style>



